`, "",{"version":3,"sources":["webpack://./src/shared/components/ncTable/partials/rowTypePartials/DatetimeDateForm.vue"],"names":[],"mappings":";AAsEA;CACA,WAAA;AACA;AAEA;CACA,kBAAA;AACA;AAEA;CACA,eAAA;AACA","sourcesContent":["<!--\n  - SPDX-FileCopyrightText: 2023 Nextcloud GmbH and Nextcloud contributors\n  - SPDX-License-Identifier: AGPL-3.0-or-later\n-->\n<template>\n\t<RowFormWrapper :title=\"column.title\" :mandatory=\"column.mandatory\" :description=\"column.description\" :width=\"2\">\n\t\t<NcDateTimePickerNative id=\"datetime-date-picker\" v-model=\"localValue\"\n\t\t\ttype=\"date\" />\n\t\t<div v-if=\"canBeCleared\" class=\"icon-close make-empty\" @click=\"emptyValue\" />\n\t</RowFormWrapper>\n</template>\n\n<script>\nimport { NcDateTimePickerNative } from '@nextcloud/vue'\nimport Moment from '@nextcloud/moment'\nimport RowFormWrapper from './RowFormWrapper.vue'\n\nexport default {\n\tcomponents: {\n\t\tNcDateTimePickerNative,\n\t\tRowFormWrapper,\n\t},\n\tprops: {\n\t\tcolumn: {\n\t\t\ttype: Object,\n\t\t\tdefault: null,\n\t\t},\n\t\tvalue: {\n\t\t\ttype: String,\n\t\t\tdefault: null,\n\t\t},\n\t},\n\tdata() {\n\t\treturn {\n\t\t}\n\t},\n\tcomputed: {\n\t\tcanBeCleared() {\n\t\t\treturn !this.column.mandatory\n\t\t},\n\t\tlocalValue: {\n\t\t\tget() {\n\t\t\t\tif (this.value !== null && this.value !== 'none') {\n\t\t\t\t\treturn Moment(this.value, 'YYYY-MM-DD').toDate()\n\t\t\t\t} else if (this.value === null && this.column.datetimeDefault === 'today') {\n\t\t\t\t\tconst dt = Moment()\n\t\t\t\t\tthis.$emit('update:value', dt.format('YYYY-MM-DD'))\n\t\t\t\t\treturn dt.toDate()\n\t\t\t\t} else {\n\t\t\t\t\treturn null\n\t\t\t\t}\n\t\t\t},\n\t\t\tset(v) {\n\t\t\t\tif (v === 'none') {\n\t\t\t\t\tthis.$emit('update:value', v)\n\t\t\t\t} else if (v) {\n\t\t\t\t\tthis.$emit('update:value', Moment(v).format('YYYY-MM-DD'))\n\t\t\t\t}\n\t\t\t},\n\t\t},\n\t},\n\tmethods: {\n\t\temptyValue() {\n\t\t\tthis.localValue = 'none'\n\t\t},\n\t},\n}\n</script>\n<style scoped>\n\n.mx-datepicker {\n\twidth: 100%;\n}\n\n.make-empty {\n\tpadding-left: 15px;\n}\n\n.make-empty:hover {\n\tcursor: pointer;\n}\n\n</style>\n"],"sourceRoot":""}]);
