`, "",{"version":3,"sources":["webpack://./src/shared/components/ncTable/partials/rowTypePartials/NumberProgressForm.vue"],"names":[],"mappings":";AAwEA;CACA,kBAAA;AACA;AAEA;AACA;EACA,eAAA;AACA;AACA","sourcesContent":["<!--\n  - SPDX-FileCopyrightText: 2023 Nextcloud GmbH and Nextcloud contributors\n  - SPDX-License-Identifier: AGPL-3.0-or-later\n-->\n<template>\n\t<RowFormWrapper :title=\"column.title\" :mandatory=\"column.mandatory\" :description=\"column.description\" :width=\"2\">\n\t\t<input v-model=\"localValue\"\n\t\t\ttype=\"number\"\n\t\t\tmin=\"0\"\n\t\t\tmax=\"100\"\n\t\t\t@input=\"enforceBounds\">\n\t</RowFormWrapper>\n</template>\n\n<script>\nimport RowFormWrapper from './RowFormWrapper.vue'\n\nexport default {\n\tcomponents: {\n\t\tRowFormWrapper,\n\t},\n\tprops: {\n\t\tcolumn: {\n\t\t\ttype: Object,\n\t\t\tdefault: null,\n\t\t},\n\t\tvalue: {\n\t\t\ttype: Number,\n\t\t\tdefault: null,\n\t\t},\n\t},\n\tdata() {\n\t\treturn {\n\t\t}\n\t},\n\tcomputed: {\n\t\tlocalValue: {\n\t\t\tget() {\n\t\t\t\tif (this.value !== null) {\n\t\t\t\t\treturn this.value\n\t\t\t\t} else {\n\t\t\t\t\tif (this.column.numberDefault !== undefined) {\n\t\t\t\t\t\tthis.$emit('update:value', this.column.numberDefault)\n\t\t\t\t\t\treturn this.column.numberDefault\n\t\t\t\t\t}\n\t\t\t\t\treturn null\n\t\t\t\t}\n\t\t\t},\n\t\t\tset(v) {\n\t\t\t\tconst parsedValue = parseInt(v)\n\t\t\t\tconst clampedValue = Math.min(Math.max(0, v), 100)\n\t\t\t\tthis.$emit('update:value', isNaN(parsedValue) ? null : parseInt(clampedValue))\n\t\t\t},\n\t\t},\n\t},\n\tmethods: {\n\t\tenforceBounds(event) {\n\t\t\tconst value = parseInt(event.target.value)\n\t\t\tif (isNaN(value)) {\n\t\t\t\tthis.$emit('update:value', null)\n\t\t\t\tevent.target.value = null\n\t\t\t\treturn\n\t\t\t}\n\t\t\tconst clampedValue = Math.min(Math.max(0, value), 100)\n\t\t\tthis.$emit('update:value', clampedValue)\n\t\t\tevent.target.value = clampedValue\n\t\t},\n\t},\n}\n</script>\n<style scoped>\n\n.hint-padding-left {\n\tpadding-left: 20px;\n}\n\n@media only screen and (max-width: 641px) {\n\t.hint-padding-left {\n\t\tpadding-left: 0;\n\t}\n}\n\n</style>\n"],"sourceRoot":""}]);
