`, "",{"version":3,"sources":["webpack://./src/shared/components/ncTable/partials/rowTypePartials/SelectionMultiForm.vue"],"names":[],"mappings":";AA6EA;CACA,kBAAA;AACA;AAEA;AACA;EACA,eAAA;AACA;AACA;AAEA;CACA,WAAA;AACA","sourcesContent":["<!--\n  - SPDX-FileCopyrightText: 2023 Nextcloud GmbH and Nextcloud contributors\n  - SPDX-License-Identifier: AGPL-3.0-or-later\n-->\n<template>\n\t<RowFormWrapper :title=\"column.title\" :mandatory=\"column.mandatory\" :description=\"column.description\">\n\t\t<NcSelect v-model=\"localValues\" :tag-width=\"80\" :options=\"getAllNonDeletedOrSelectedOptions\" :multiple=\"true\" :aria-label-combobox=\"t('tables', 'Options')\" />\n\t</RowFormWrapper>\n</template>\n\n<script>\nimport { NcSelect } from '@nextcloud/vue'\nimport RowFormWrapper from './RowFormWrapper.vue'\n\nexport default {\n\tname: 'SelectionMultiForm',\n\tcomponents: {\n\t\tNcSelect,\n\t\tRowFormWrapper,\n\t},\n\tprops: {\n\t\tcolumn: {\n\t\t\ttype: Object,\n\t\t\tdefault: null,\n\t\t},\n\t\tvalue: {\n\t\t\ttype: Array,\n\t\t\tdefault: null,\n\t\t},\n\t},\n\tcomputed: {\n\t\tlocalValues: {\n\t\t\tget() {\n\t\t\t\tif (this.value !== null) {\n\t\t\t\t\treturn this.column.getObjects(this.value)\n\t\t\t\t} else {\n\t\t\t\t\tthis.$emit('update:value', this.column.default())\n\t\t\t\t\treturn this.column.getDefaultObjects()\n\t\t\t\t}\n\t\t\t},\n\t\t\tset(v) {\n\t\t\t\tthis.$emit('update:value', this.getIdArrayFromObjects(v))\n\t\t\t},\n\t\t},\n\t\tgetOptions() {\n\t\t\treturn this.column.selectionOptions || null\n\t\t},\n\t\tgetAllNonDeletedOrSelectedOptions() {\n\t\t\tconst options = this.getOptions?.filter(item => {\n\t\t\t\treturn !item.deleted || this.optionIdIsSelected(item.id)\n\t\t\t}) || []\n\n\t\t\toptions.forEach(opt => {\n\t\t\t\tif (opt.deleted) {\n\t\t\t\t\topt.label += ' '\n\t\t\t\t}\n\t\t\t})\n\t\t\treturn options\n\t\t},\n\t},\n\tmethods: {\n\t\toptionIdIsSelected(id) {\n\t\t\t// check if the given id is selected (in the value array)\n\t\t\treturn this.values.includes(id)\n\t\t},\n\t\tgetIdArrayFromObjects(objects) {\n\t\t\tconst ids = []\n\t\t\tobjects.forEach(o => {\n\t\t\t\tids.push(o.id)\n\t\t\t})\n\t\t\treturn ids\n\t\t},\n\t},\n}\n</script>\n<style scoped>\n\n.hint-padding-left {\n\tpadding-left: 20px;\n}\n\n@media only screen and (max-width: 641px) {\n\t.hint-padding-left {\n\t\tpadding-left: 0;\n\t}\n}\n\n.multiselect {\n\twidth: 100%;\n}\n\n</style>\n"],"sourceRoot":""}]);
